// middleware.js
import { NextResponse } from 'next/server';

const PROTECTED = ['/checkout', '/profile', '/my-orders', '/wishlist','/cart','/seller','/admin'];

export function middleware(req) {
  const { pathname } = req.nextUrl;
  // Jangan guard /login atau /register
  const needsAuth = PROTECTED.some(p => pathname.startsWith(p));
  if (!needsAuth) return NextResponse.next();

  const token = req.cookies.get('gt_auth')?.value;
  if (!token) {
    const url = new URL('/login', req.url);
    url.searchParams.set('next', pathname);
    return NextResponse.redirect(url);
  }

  // (opsional) Soft check role admin dengan decode payload (tanpa verif).
  if (pathname.startsWith('/admin')) {
    try {
      const [, payloadB64] = token.split('.');
      const json = JSON.parse(Buffer.from(payloadB64, 'base64url').toString('utf8'));
      const roles = json?.roles || [];
      if (!roles.includes('admin')) {
        return NextResponse.redirect(new URL('/', req.url));
      }
    } catch {
      return NextResponse.redirect(new URL('/', req.url));
    }
  }


  return NextResponse.next();
}


export const config = {
  matcher: ['/checkout/:path*','/profile/:path*','/my-orders/:path*','/wishlist/:path*','/cart/:path*','/seller/:path*','/admin/:path*'],
};
